
import { defineComponent } from 'vue'
export default defineComponent({
    props:{
        char: Object
    },
    methods: {
        getWeaponStar(rarity:number):string{
            if(rarity === 5){
                return "five_star.png";
            }else if(rarity === 4){
                return "four_star.png";
            }else if(rarity === 3){
                return "three_star.png";
            }else if(rarity === 2){
                return "two_star.png";
            }else if(rarity === 1){
                return "one_star.png"
            }else {
                return ""
            }
        }
    }
})
