
import { defineComponent } from "@vue/runtime-core"
import {mapActions} from "vuex"
export default defineComponent({
    data(){
        return {
            search: "" as string
        }
    },

    methods: {
         ...mapActions(["getUserInfo"]),
        searchUser():void {
            this.getUserInfo(this.search)
            this.search = '';
        },
    }
})
